@import url(https://fonts.googleapis.com/css2?family=Nova+Mono&display=swap);
*, *::after, *::before{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body{
  min-height: 100vh;
  max-width: 40rem;
  margin: 0 auto;
  padding: 2rem 1rem;
  font-family: 'Nova Mono', monospace;
  background-color: rgba(40, 40, 40, 40);
  color: #FFF;

}
button,input,label{
  display: block;
  border: none;
  outline: none;
  background-color: transparent;
  font-family: inherit;
  font-size: inherit;
  color: inherit;
}
button{
  cursor: pointer;
}
img{
  display: block;
  width: 100%;
}
ul{
  list-style: none;
}
.app{
    display: grid;
    grid-template-rows: 1fr auto;
  }
  h1{
    font-weight: bold;
    font-size: 2.25rem;
    text-align: center;
    letter-spacing: -0.045em;
    margin-bottom: 3.6rem;
  }
 
  .filter-buttons-container{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    border-bottom: 1px solid #BDBDBD;
  }
  .filter-buttons-container button{
    padding-bottom: .8rem;
    position: relative;
    text-align: center;
    min-width: 89px;
   
  }
  .filter-buttons-container button.active::after{
    background-color: #2F80ED;
    width: 100%;
    height: 4px;
    content: '';
    display: block;
    position: absolute;
    bottom: 0;
    border-radius: 4px 4px 0px 0px;
  
  }
form{
    display: flex;
    justify-content: space-between;
    margin: 1.125rem 0 2rem 0;
    height: 3.5rem;
}
form input{
    border: 1px solid #BDBDBD;
    border-radius: 12px;
    width: calc(100% - 8.4rem);
    padding: 1.125rem .75rem;   
}
input::-webkit-input-placeholder{
    font-family: inherit;
    font-size: .9rem;
    color: inherit;
}
input:-ms-input-placeholder{
    font-family: inherit;
    font-size: .9rem;
    color: inherit;
}
input::placeholder{
    font-family: inherit;
    font-size: .9rem;
    color: inherit;
}
form input:focus{
    border: 2px solid #2F80ED;
}
form button{
    background-color:#2F80ED;
    width: 6.8rem;
    border-radius: 12px;
    color: inherit;
    font-size: .9rem;
}
.to-do-list-container{
    display: flex;
    flex-direction: column;
}
.void-list{
    text-align: center;
    margin-top: 3rem;
}
.delete-all{
    width: 7.75rem;
    height: 2.5rem;
    margin: 2.3rem 0  0 auto;
    background: #EB5757;
    border-radius: 4px;
    font-weight: 600;
    font-size: .9rem;
    color: #FFFFFF;
    display: flex;
    align-items: center;
    justify-content: center;

}

.delete-all span {
    display: block;
   
    
}

.delete-all span:first-child {
    margin-right: .5rem;
    width: .7rem;
    
}
.to-do{
    display: flex;
    flex-wrap: wrap;
    cursor: pointer;
}
.to-do:not(:last-child){
    margin-bottom: 1.5rem;
}
.to-do input,
.to-do label{
    display: inline-block;
    cursor: inherit;
 
}

.to-do input:checked + label{
    text-decoration: line-through;
}
.to-do label{
    font-weight: 500;
    font-size: 1.1rem;
    margin-left: .4rem;
}
.to-do button{
    width: 1rem;
    margin-left: auto;
    overflow: hidden;
}
