@import url(https://fonts.googleapis.com/css2?family=Nova+Mono&display=swap);

*, *::after, *::before{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body{
  min-height: 100vh;
  max-width: 40rem;
  margin: 0 auto;
  padding: 2rem 1rem;
  font-family: 'Nova Mono', monospace;
  background-color: rgba(40, 40, 40, 40);
  color: #FFF;

}
button,input,label{
  display: block;
  border: none;
  outline: none;
  background-color: transparent;
  font-family: inherit;
  font-size: inherit;
  color: inherit;
}
button{
  cursor: pointer;
}
img{
  display: block;
  width: 100%;
}
ul{
  list-style: none;
}